<style>
</style>

<script>
import { useQuasar } from "quasar";
import { ref } from "vue";
import Header from "../../components/Header.vue";

const setup = {
  origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_CLIENTS
};

export default {
  name: "Home",
  search: "Pesquisar",
  components: {
    Header,
  },
  setup() {
    const $q = useQuasar();
    const submitting = ref(false);

    return {
      setup: setup,
      submitting,
      pageTitle: "Cadastro de Clientes",
      pageSubTitle: "Pessoa autorizada",
      showNotif(msg, color, icon) {
        $q.notify({
          message: msg,
          color: color,
          icon: icon,
        });
      },
    };
  },
  data() {
    return {
      name: null,
      email: null,
      cpf: null,    
      rg: null,   
      phone: null,
      phone2: null,
      address: null,
      number: null,
      complement: null,
      district: null,
      city: null,
      zipcode: null,
      othername: null,
      otherphone: null,
      status: 'active'
    };
  },
  methods: {
    async createclient() {
      this.submitting = true;
      console.log("salvo");
      const data = {
        name: this.name,
        email: this.email,        
        cpf: this.cpf,
        rg: this.rg,
        phone: this.phone,
        phone2: this.phone2,
        address: this.address,
        number: this.number,
        complement: this.complement,
        district: this.district,
        city: this.city,
        zipcode: this.zipcode,
        othername: this.othername,
        otherphone: this.otherphone,
        status: this.status

      };


      const dataJson = JSON.stringify(data);

      console.log(dataJson);

      if (data.name && data.cpf && data.email && data.phone) {
        const req = await fetch(this.setup.url, {
          method: "POST",
          mode: 'cors',
          headers: {
            'Origin': this.setup.origin,
            'Accept': 'application/json, text/javascript, */*; q=0.01',
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },  
          body: dataJson,
        });

        const res = await req.json();

        if (res.status === "success") {
          this.name = "";
          this.email = "";
          this.cpf = "";
          this.rg = "";         
          this.phone = "";
          this.phone2 = "";
          this.address = "";
          this.number = "";
          this.complement = "";
          this.district = "";
          this.city = "";
          this.zipcode = "";
          this.othername = "";
          this.otherphone = "";
          this.status = "";
          this.showNotif(
            res.data,
            "green",
            "las la-thumbs-up"
          );
          this.$router.go(-1);
        } else {
          this.showNotif(
            res.data,
            "red",
            "las la-bomb"
          );
        }
      } else {
        this.showNotif(
          "Por favor. Os campos com * são obrigatórios.",
          "red",
          "las la-exclamation-triangle"
        );
      }
      this.submitting = false;
    },
  },

  mounted() {},
};
</script>
